import React from 'react'
import { Pagination } from 'react-bootstrap'


function Paginate({page, pages, maxPageDisplay=5, category=null}) {

    let url = category ?  `${process.env.PUBLIC_URL}/category/${category.toLowerCase()}/` : `${process.env.PUBLIC_URL}/`

    return (pages > 1 && (
        <Pagination>

            {/* First */}
            {pages !== 1 && page !== 1 ? (
                <Pagination.First href={`${url}1/`}><i className="fas fa-angle-double-up"></i></Pagination.First>
            ) : (
                <Pagination.First disabled><i className="fas fa-angle-double-up"></i></Pagination.First>
            )}


            {/* Prev */}
            {page > 1 ? (
                <Pagination.Prev href={`${url}${page-1}/`}><i className="fas fa-angle-up"></i></Pagination.Prev>
            ) : (
                <Pagination.Prev disabled><i className="fas fa-angle-up"></i></Pagination.Prev>
            )}
            
            
            {/* Pages */}
            {[...Array(pages).keys()].map((x) => (page === x+1 ? (

                <Pagination.Item key={x+1} active href={`${url}${x+1}/`}>{x+1}</Pagination.Item>
            
                ) : x+1 > page && x+1 <= page + maxPageDisplay ? (
                
                    <React.Fragment key={x+1}>

                        <Pagination.Item href={`${url}${x+1}/`}>{x+1}</Pagination.Item>  

                        {x+1 === page + maxPageDisplay && x+1 < pages && (
                            <Pagination.Ellipsis href={`${url}${page+(maxPageDisplay+1)}/`} />
                        )}            

                    </React.Fragment> 

                ) : x+1 < page && x+1 >= page - maxPageDisplay && (
                    
                    <React.Fragment key={x+1} >

                        {x+1 === page - maxPageDisplay && x+1 > 1 && (
                            <Pagination.Ellipsis href={`${url}${page-(maxPageDisplay+1)}/`} />
                        )}

                        <Pagination.Item href={`${url}${x+1}/`}>{x+1}</Pagination.Item>
                        
                    </React.Fragment>   

                )
            ))}


            {/* Next */}
            {page < pages ? (
                <Pagination.Next href={`${url}${page+1}/`}><i className="fas fa-angle-down"></i></Pagination.Next>
            ) : (
                <Pagination.Next disabled><i className="fas fa-angle-down"></i></Pagination.Next>
            )}


            {/* Last */}
            {pages !== page ? (
                <Pagination.Last href={`${url}${pages}/`}><i className="fas fa-angle-double-down"></i></Pagination.Last>
            ) : (
                <Pagination.Last disabled><i className="fas fa-angle-double-down"></i></Pagination.Last>
            )}

        </Pagination>
    ))
}

export default Paginate